import {
  IntrinsicNodeProps,
  View,
  ElementNode,
  activeElement,
} from "@lightningjs/solid";
import styles from "./Thumnail.styles";
import { createEffect, on } from "solid-js";
import { assertTruthy } from "@lightningjs/renderer/utils";
import { useNavigate } from '@solidjs/router';
import { setMovieDetailState } from "../../state";

export default function Thumbnail(props: IntrinsicNodeProps) {
  const navigate = useNavigate();
  let focusRingRef;
  createEffect(on(activeElement, (elm) => {
    assertTruthy(elm);
    setTimeout(() => {
      if (elm.heroContent) {
        focusRingRef.parent = elm.parent;
        focusRingRef.alpha = 1;
        focusRingRef.scale = 1;
        focusRingRef.x = elm.x! - 2;
        focusRingRef.y = elm.y! - 2;
        focusRingRef.width = elm.width! + 4;
        focusRingRef.height = elm.height! + 4;
        focusRingRef.createAnimation({ scale: 1.03}).start();
        // focusRingRef.zIndex = (elm.zIndex! - 0.00000001);
      } else {
        focusRingRef.alpha = 0;
      }
    }, 10)
  }, { defer: true}))
  function onRowFocus(this: ElementNode) {
    this.children.selected?.setFocus();
  }

  function viewDetailVideo(this :any) {
    console.log(this);
    setMovieDetailState(this.data);
    navigate(`/movie/${this.data.id}`);
  }

  // console.log('props',props)
  return (
    <View {...props} animate style={styles.Thumbnail} onEnter={viewDetailVideo}>
      <View
        onFocus={onRowFocus}
        {...props}
        animate
        style={styles.Thumbnail}
      ></View>
      <View ref={focusRingRef} style={styles.FocusRing}></View>
    </View>
  );
}
